.background {
  position: fixed;
  display: block;
  z-index: 998;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.863);
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.loginForm {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.input {
  min-width: 300px;
  padding: 5px 10px;
  border-radius: 5px;
  outline: none;
  border: 1px solid #54c263;
}

.input:not(first-child) {
  margin-top: 10px;
}

.notification--error {
  margin-top: 10px;
  color: #bf1650;
}

.notification--error::before {
  content: "⚠ ";
}

//modal add new service

.add_service_form {
  position: absolute;
  min-width: 280px;
  padding: 10px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  border-radius: 10px;
  background-color: #fff;
  color: #000000;
}

.fieldset {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-top: 5px;
}
